body {
  width: 100vw;
}

.App {
  text-align: center;
  padding: 1.5em 3em;
  color: #333;
}

#root {
  width: 100%;
  height: 100%;
  overflow: auto;
}

@media (max-width: 650px) {
  .App {
    padding: 0;
    background-color: #eeeff3;
  }
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
